@import "./reset";

@import url("https://fonts.googleapis.com/css2?family=Lugrasimo&display=swap");

@import url("https://use.typekit.net/hrg3vca.css");

html,
body,
* {
  box-sizing: border-box;
}

p {
  font-family: "Lugrasimo", cursive;
  font-size: 36px;
}

.container {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;

  .cytat {
    position: absolute;
    top: 77%;
    display: flex;
    flex-direction: column;
  }

  .fota {
    position: absolute;
    top: 1%;
    width: 100%;
    height: auto;
    margin-bottom: 5vw;
    filter: sepia(1);
  }

  p {
    font-size: calc(15px + 1vw);
    font-family: "basic-sans", sans-serif;
    font-weight: 400;
    font-style: normal;
    align-self: flex-start;
    justify-self: flex-end;
    margin-bottom: 2vw;
    padding: 0 15vw;
  }

  span {
    font-size: 1.5vw;
    font-family: "basic-sans", sans-serif;
    font-weight: 400;
    font-style: italic;
    align-self: flex-end;
    margin-top: 3vw;
    margin-right: 5vw;
  }

  h1 {
    position: absolute;
    font-family: "Lugrasimo", cursive;
    font-size: 10vw;
    opacity: 0.8;
    top: 5%;

    span {
      font-family: "Lugrasimo", cursive;
      font-size: 5vw;
    }
  }
}

.background {
  position: absolute;
  z-index: -1;
  width: 100vw;
  height: 100vh;

  img {
    position: absolute;
    width: 100vw;
    // mix-blend-mode: multiply;
    filter: saturate(0);

    &.rysunek {
      opacity: 0.3;
      top: 0;
    }

    &.background {
      z-index: -2;
    }
  }
}
